import React from 'react';
import { Route ,Switch } from "react-router-dom";
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "../node_modules/bootstrap/dist/css/all.css";
import "../node_modules/bootstrap/dist/css/animate.css";
import "../node_modules/bootstrap/dist/css/responsive.css";
import "../node_modules/bootstrap/dist/js/bootstrap.bundle.min.js";
import './index.css';
import Home from './Home';
import About from './About';
import Subscriptions from './Subscriptions';
import Blog from './Blog';
import Questions from './Questions';
import BlogDetails from './BlogDetails';
import Login from './Login';
import Dashboard from './Dashboard';
import Special_reports from './Special_reports';
import Special_report_details from './Special_report_details';
import Previous_newsletters from './Previous_newsletters';
import Previous_newsletter_details from './Previous_newsletter_details';
import Trade_updates from './Trade_updates';
import Trade_update_details from './Trade_update_details';
import BlogSearch from './BlogSearch';
import Change_pass from './Change_pass';
import StripeContainer from './components/StripeContainer';
import Checkout from './components/Checkout';
//const webpack = require('webpack');

const App =() => {
	return (
	<>
	<Switch>
		<Route exact path="/" component={Home} />
		<Route exact path="/about" component={About} />
		<Route exact path="/subscriptions" component={Subscriptions} />
		<Route exact path="/blog" component={Blog} />
		<Route exact path="/questions" component={Questions} />
		<Route exact path="/blogDetails" component={BlogDetails} />
		<Route exact path="/login" component={Login} />
		<Route exact path="/dashboard" component={Dashboard} />
		<Route exact path="/special_reports" component={Special_reports} />
		<Route exact path="/special_report_details" component={Special_report_details} />
		<Route exact path="/previous_newsletters" component={Previous_newsletters} />
		<Route exact path="/previous_newsletter_details" component={Previous_newsletter_details} />
		<Route exact path="/trade_updates" component={Trade_updates} />
		<Route exact path="/trade_update_details" component={Trade_update_details} />
		<Route exact path="/change_pass" component={Change_pass} />
		<Route exact path="/blogSearch" component={BlogSearch} />
		<Route exact path="/stripeContainer" component={StripeContainer} />
		<Route exact path="/checkout" component={Checkout} />
		
	</Switch>
	</>
	);
};

export default App;
