import React, { useState, useEffect} from 'react';
import { NavLink , Redirect} from "react-router-dom";
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import banner1 from './images/banner-1.png';
import analysis from './images/analysis.png';
import global_network from './images/global-network.png';
import statistics from './images/statistics.png';
import marketing from './images/marketing.png';
import fund from './images/fund.png';
import goal from './images/goal.png';
import book from './images/book.png';
import startup from './images/startup.png';
import analysis_small from './images/analysis-small.png';
import why_us from './images/why-us.png';
import update from './images/update.png';
import quotes from './images/quotes.png';
import reader from './images/reader.png';
import closeimg from './images/close.png';
import Navbar from './Navbar';
import Footer from './Footer';
import axios from 'axios';
var Carousel = require('react-responsive-carousel').Carousel;

const Home = () => {
	
	const [Home_Banner_Image, setHome_Banner_Image] = useState("");
	const [Home_Heading, setHome_Heading] = useState("");
	const [Home_Top_Heading_Content, setHome_Top_Heading_Content] = useState("");
	const [Our_Readers_Get_Content, setOur_Readers_Get_Content] = useState("");
	const [Planning_Strategies, setPlanning_Strategies] = useState("");
	const [Business_Growth_Chart, setBusiness_Growth_Chart] = useState("");
	const [Business_Goals, setBusiness_Goals] = useState("");
	const [Great_Knowledge, setGreat_Knowledge] = useState("");
	const [Boost_Your_Business, setBoost_Your_Business] = useState("");
	const [Data_Analysis, setData_Analysis] = useState("");
	const [Our_Subscription, setOur_Subscription] = useState("");
	const [Insightful_Analysis, setInsightful_Analysis] = useState("");
	const [Investment_Strategies, setInvestment_Strategies] = useState("");
	const [Timely_Updates, setTimely_Updates] = useState("");
	const [Why_Us, setWhy_Us] = useState("");
	const [Base_Plan, setBase_Plan] = useState("");
	const [Advanced_Plan, setAdvanced_Plan] = useState("");
	const [Advanced_Plan_Price, setAdvanced_Plan_Price] = useState("");
	const [Subscribe_Now_Content, setSubscribe_Now_Content] = useState("");
	const [Recent, setRecent] = useState(0);
	const [Testimonial, setTestimonial] = useState(0);
	const [Lower_slider, setLower_slider] = useState(0);
	const [BlogId, setBlogId] = useState(0);
	const [redirect, setRedirect] = useState(false);
	const [redirect2, setRedirect2] = useState(false);
	const [Error_msg, setError] = useState("");
	const [Success_msg, setSuccess] = useState("");
	const [email, setEmail] = useState("");
	const [username, setUsername] = useState("");
		
	  useEffect(() => {
		axios.post('home')
		.then(res => {
			
			if(res.data.page_data !=='')
			{
				//console.log(res.data.page_data);
				let pageData = res.data.page_data;
				let subscription_plan = res.data.subscription_plan;
				let recent_post = res.data.recent_post;
				let testimonial = res.data.testimonial_content;
				
				let h =0;
				let arr12 =[];
				let slider =[];
				
				
				//console.log(testimonial);
				for(h in testimonial)
				{
					
					if(h ==2)
					{
						let list = (<div className="carousel-item active">
							  <p>{testimonial[h].msg}</p>
							  <div className="reader-name">{testimonial[h].name}</div>
							  <span className="reader-img mobile-view"><img src={testimonial[h].img} alt="images"/></span>
							</div>)
							
							let sli = (<li data-target="#carouselExampleIndicators" data-slide-to={h} className="active"><span className="reader-img"><img src={testimonial[h].img} alt="images"/></span>
							</li>)
							
						arr12.push(list);
						slider.push(sli);
						
					}else{
						let list = (<div className="carousel-item">
							  <p>{testimonial[h].msg}</p>
							  <div className="reader-name">{testimonial[h].name}</div>
							  <span className="reader-img mobile-view"><img src={testimonial[h].img} alt="images"/></span>
							</div>)
							
							let sli = (<li data-target="#carouselExampleIndicators" data-slide-to={h}><span className="reader-img"><img src={testimonial[h].img} alt="images"/></span>
							</li>)
							
						arr12.push(list);
						slider.push(sli);
						
					}
					h++;
				}
				//console.log(arr12);
				//console.log(slider);
				setTestimonial(arr12);
				setLower_slider(slider);
				
				
				//console.log(testimonial);
				let j =0;
				for(j in subscription_plan)
				{
					if(subscription_plan[j].plan_name === 'Basic')
					{
						let response = subscription_plan[j].plan_description.split(',');
						let i =0;
						let arr =[];
						 for(i in response)
						 {
							 let li = (<li>{response[i]}</li>)
							 arr.push(li);
							 i++;
						 }
						let base_desc =( <ul className="sub-doc">
						{arr}
						</ul>)
						
						
						setBase_Plan(base_desc);
						
						
					}else if(subscription_plan[j].plan_name === 'Advanced'){
						
						let response = subscription_plan[j].plan_description.split(',');
						let i =0;
						let arr =[];
						 for(i in response)
						 {
							 let li = (<li>{response[i]}</li>)
							 arr.push(li);
							 i++;
						 }
						let base_desc =( <ul className="sub-doc">
						{arr}
						</ul>)
						
						setAdvanced_Plan(base_desc);
						setAdvanced_Plan_Price(subscription_plan[j].plan_price);
					}
					j++;
				}
				//console.log(pageData);
				let i =0;
				for(i in pageData)
				{
					
					if(pageData[i].section_name === 'Home_Banner_Image')
					{
						 setHome_Banner_Image(pageData[i].content_image);
						 
					}else if(pageData[i].section_name === 'Home_Heading')
					{
						//console.log(Home_Heading);
						
						setHome_Heading(pageData[i].content);
					}else if(pageData[i].section_name === 'Home_Top_Heading_Content')
					{
						setHome_Top_Heading_Content(pageData[i].content);
					}else if(pageData[i].section_name === 'Our_Readers_Get_Content')
					{
						setOur_Readers_Get_Content(pageData[i].content);
					}else if(pageData[i].section_name === 'Planning_Strategies')
					{
						setPlanning_Strategies(pageData[i].content);
					}else if(pageData[i].section_name === 'Business_Growth_Chart')
					{
						setBusiness_Growth_Chart(pageData[i].content);
					}else if(pageData[i].section_name === 'Business_Goals')
					{
						setBusiness_Goals(pageData[i].content);
					}else if(pageData[i].section_name === 'Great_Knowledge')
					{
						setGreat_Knowledge(pageData[i].content);
					}else if(pageData[i].section_name === 'Boost_Your_Business')
					{
						setBoost_Your_Business(pageData[i].content);
					}else if(pageData[i].section_name === 'Data_Analysis')
					{
						setData_Analysis(pageData[i].content);
					}else if(pageData[i].section_name === 'Our_Subscription')
					{
						setOur_Subscription(pageData[i].content);
					}else if(pageData[i].section_name === 'Insightful_Analysis')
					{
						setInsightful_Analysis(pageData[i].content);
					}else if(pageData[i].section_name === 'Investment_Strategies')
					{
						setInvestment_Strategies(pageData[i].content);
					}else if(pageData[i].section_name === 'Timely_Updates')
					{
						setTimely_Updates(pageData[i].content);
					}else if(pageData[i].section_name === 'Why_Us')
					{
						setWhy_Us(pageData[i].content);
					}else if(pageData[i].section_name === 'Subscribe_Now_Content')
					{
						//console.log(pageData[i].content);
						setSubscribe_Now_Content(pageData[i].content);
					}
					i++;
				}
				
				
				let k =0;
				let arr =[];
				for(k in recent_post)
				{
					let bloid = recent_post[k].blog_id;
					//console.log(bloid);
					let list = (<div className="item">
						<div className="row no-gutters">
							<div className="col-lg-6">
								<div className="update-img"><img src={recent_post[k].blog_img} alt="images" className="img-fluid"/></div>
							</div>
							<div className="col-lg-6 align-self-center">
								<div className="update-text">
									<h2>{recent_post[k].title}</h2>
									<h6>Admin - {recent_post[k].created_at}</h6>
									<p>{recent_post[k].short_desc}</p>
									<button onClick={()=>setBlogid(bloid)} className="read-more">Read More </button>
								</div>
							</div>
						</div>
					</div>)
					arr.push(list);
					k++;
				}
				setRecent(arr);

				
				
			}
		
		})
		.catch(err => {
			console.log(err)
		})
		
	});
	
	const Freesubscribe = async (e:SyntheticEvent) => {
		e.preventDefault();
		//console.log('works');
		const data = {
			email: email,
			name: username
		}
		console.log(data);
		axios.post('freeuser', data)
		.then(res => {
			
			if(res.data.userid !=='')
			{
				setSuccess("User subscribed");
				window.location.reload();
				
			}else{
				setError("Subscription fail");
			}
		
		})
		.catch(err => {
			console.log(err)
		})
		
	};
	
	
		function setBlogid(blogid) {
			localStorage.removeItem('BlogId');
			localStorage.setItem('BlogId', blogid);
			setRedirect(true);
			//alert(`hello, ${blogid}`);
		  }
  
		if (redirect) {
			return <Redirect to="/blogDetails"/>;
		}
		
		if (redirect2) {
        return <Redirect to="/subscriptions"/>;
		}
	
	return (
	<>
	<Navbar />
		<div className="bannerbox">
			<div id="carouselExampleCaptions" className="carousel slide" data-ride="carousel">
			  <div className="carousel-inner">
			    <div className="carousel-item active">
			      <img src={Home_Banner_Image} className="d-block w-100" alt="slider images"/>
			      <div className="carousel-caption-new d-none d-md-block">
			        <h1>Welcome to Investoroo</h1>
			        <p>{Home_Heading}</p>
			        <NavLink className="btn btn-learn" to="/" role="button">Learn More</NavLink>
			      </div>
			    </div>
			  </div>
			</div>
		</div>
		
		<section className="year-experience-sec">
			<div className="container">
				<div className="row align-items-center">
					<div className="col-lg-3">
						<div className="experbox ml-lg-auto m-auto">
							<h1>10</h1>
							<h5>Year's of experience </h5>
						</div>
					</div>
					<div className="col-lg-4">
						<div className="year-struggle">
							<h3>Profitable investment ideas using <br/> data-driven analysis</h3>
						</div>
					</div>
					<div className="col-lg-5">
						<div className="struggle-text">
						{Home_Top_Heading_Content} 
						</div>
					</div>
				</div>
			</div>
		</section>
	
		<section className="analysis">
			<div className="container">
				<div className="row shadowstyle">
					<div className="col-md-4">
						<div className="boxstyle">
							<img src={analysis} alt="images"/>
							<h3>Insightful Analysis</h3>
							<p>{Insightful_Analysis}</p>
						</div>
					</div>
					<div className="col-md-4">
						<div className="boxstyle">
							<img src={statistics} alt="images"/>
							<h3>Business Advantages</h3>
							<p>{Investment_Strategies}</p>
						</div>
					</div>
					<div className="col-md-4">
						<div className="boxstyle no-border">
							<img src={global_network} alt="images"/>
							<h3>Global Thinking</h3>
							<p>{Timely_Updates}</p>
						</div>
					</div>
				</div>
			</div>
		</section>

		<section className="reder-gets">
			<div className="container">
				<div className="row">
					<div className="col-lg-12 redergets-txt">
						<h1>Our readers get</h1>
						<p>{Our_Readers_Get_Content}</p>
					</div>
					<div className="col-lg-4">
						<div className="regrergets-box">
							<img src={marketing} alt="images"/>
							<h1>Planning Strategies</h1>
							<p>{Planning_Strategies}</p>
							<NavLink className="btn btn-sample" to="/" role="button">See Sample</NavLink>
						</div>
					</div>
					<div className="col-lg-4">
						<div className="regrergets-box">
							<img src={fund} alt="images"/>
							<h1>Business Growth Chart</h1>
							<p>{Business_Growth_Chart}</p>
							<NavLink className="btn btn-sample" to="/" role="button">See Sample</NavLink>
						</div>
					</div>
					<div className="col-lg-4">
						<div className="regrergets-box">
							<img src={goal} alt="images"/>
							<h1>Business Goal</h1>
							<p>{Business_Goals}</p>
							<NavLink className="btn btn-sample" to="/" role="button">See Sample</NavLink>
						</div>
					</div>
				</div>
			</div>
		</section>

		<section className="why-us">
			<div className="container">
				<div className="row">
					<div className="col-lg-12 mb-3">
						<div className="webhd-withline"> Why Us </div>
						<p>{Why_Us}</p>
					</div>
				</div>
				<div className="row">
					<div className="col-lg-7">
						<div className="why-us-text">
							<div className="why-box">
								<img src={book} alt="images" className="img-fluid"/>
								<h3>Great knowledge</h3>
								<p>{Great_Knowledge}</p>
							</div>
							<div className="why-box">
								<img src={startup} alt="images" className="img-fluid"/>
								<h3>Boost you business</h3>
								<p>{Boost_Your_Business}</p>
							</div>
							<div className="why-box">
								<img src={analysis_small} alt="images" className="img-fluid"/>
								<h3>Data analysis</h3>
								<p>{Data_Analysis}</p>
							</div>
						</div>
					</div>
					<div className="col-lg-5">
						<div className="why-us-img"><img src={why_us} alt="images" className="img-fluid"/></div>
					</div>
				</div>
			</div>
		</section>

		<section className="subscription-sec">
			<div className="container">
				<div className="row">
					<div className="col-lg-12 mb-3">
						<div className="webhd-withline">Our Subscription </div>
						<p>{Our_Subscription}</p>
					</div>
				</div>
				<div className="row">
					<div className="col-lg-4 align-self-center mb-3">
						<div className="acquire">
							Acquire your Investment goals
						</div>
					</div>
					<div className="col-lg-4 mb-3">
						<div className="price-box">
							<h2>Basic</h2>
							<h1>Free Forever</h1>
							<p>{Base_Plan}</p>
							<a className="btn subsc-now" data-toggle="modal" data-target="#subModal" role="button"> Subscribe Now </a>
						</div>
					</div>
					<div className="col-lg-4 mb-3">
						<div className="price-box">
							<h2>Advance</h2>
							<h1>$ {Advanced_Plan_Price}/year</h1>
							<p>{Advanced_Plan}</p>
							<NavLink className="btn subsc-now" to="/checkout" role="button"> Subscribe Now </NavLink>
						</div>
					</div>
				</div>
			</div>
		</section>

		<section className="updatesfrom-sec">
			<div className="container">
				<div className="row">
					<div className="col-lg-12">
						<div className="webhd-withline mb-3 pl-4">Updates from <span> @Investoroo </span></div>
					</div>
				</div>
					<Carousel showArrows={true} >
						{Recent}
					</Carousel>
			</div>
		</section>
		
		<section className="readersay-sec" style={{position: 'relative'}}>
			<div className="container">
				<div className="row justify-content-center">
					<div className="col-lg-9 webhd">What our readers say</div>
				</div>
				<div className="row justify-content-center">
					<div className="col-lg-9">
						<span className="quotes-box"><img src={quotes} alt="images" className="img-fluid"/></span>
						<div id="carouselExampleIndicators" className="carousel slide" data-ride="carousel">
						 
						  <div className="carousel-inner">
						  {Testimonial}
						  </div>
						  <ol className="carousel-indicators">
						  {Lower_slider}
						  </ol>
						  
						</div>
					</div>
				</div>
			</div>
		</section>
		
		<div className="modal fade" id="subModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
	  <div className="modal-dialog" role="document">
		<div className="modal-content">
		  <div className="modal-header-new">
			<button type="button" className="close" data-dismiss="modal" aria-label="Close">
			  <span aria-hidden="true"><img src={closeimg} alt="cross icon" /></span>
			</button>
		  </div>
		  <div className="modal-body">
			<div className="subsc-popbox">
				<h2>Subscribe for latest Updates</h2>
				<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.</p>
				<div className="success_msg"> {Success_msg}</div>
				<div className="error_msg"> {Error_msg}</div>
				<div className="subpop-form">
					<form onSubmit={Freesubscribe} >
					  <div className="row">
						<div className="col-lg-12 mb-3">
						  <input type="text" className="form-control" placeholder="Name" onChange={e => setUsername(e.target.value)} />
						</div>
						<div className="col-lg-12 mb-3">
						  <input type="text" className="form-control" placeholder="Email" onChange={e => setEmail(e.target.value)} />
						</div>
						
						<button className="subsc-btn" >Subscribe Now</button>
					  </div>
					</form>
				</div>
			</div>
		  </div>
		</div>
	  </div>
	</div>
	
		<section className="mail-sbe-sec">
			<div className="container">
				<button className="blog-suscribe mt-5" onClick ={()=>setRedirect2(true)} >Subscribe</button>
			</div>
		</section>
	<Footer />
	</>
	);
};

export default Home;
