import React, {useState, useEffect} from 'react';
import { NavLink, Redirect } from "react-router-dom";
import about_bnr from './images/about-bnr.png';
import Navbar from './Navbar';
import Footer from './Footer';
import axios from 'axios';
//import './App.css';

const Questions = () => {
	
	const [Banner_Image, setBanner_Image] = useState("");
	const [Banner_Content , setBanner_Content ] = useState("");
	const [Contact_Middle_heading, setContact_Middle_heading] = useState("");
	const [Subscribe_Now_Content, setSubscribe_Now_Content] = useState("");
	const [Faq_content, setFaq_content] = useState("");
	const [redirect2, setRedirect2] = useState(false);
	const [Error_msg, setError] = useState("");
	const [Success_msg, setSuccess] = useState("");
	
	  useEffect(() => {
		axios.post('contact_us')
		.then(res => {
			
			if(res.data.page_data !=='')
			{
				//console.log(res.data.page_data);
				let pageData = res.data.page_data;
				let faq_details = res.data.faq;
				let i =0;
				for(i in pageData)
				{
					//console.log(pageData[i]);
					if(pageData[i].section_name === 'Banner_Image')
					{
						 setBanner_Image(pageData[i].content_image);
						 
					}else if(pageData[i].section_name === 'Banner_Content ')
					{
						setBanner_Content (pageData[i].content);
					}else if(pageData[i].section_name === 'Subscribe_Now_Content')
					{
						setSubscribe_Now_Content(pageData[i].content);
					}
					i++;
				}
				
				
				let j =0;
				let ques_arr =[];
				for(j in faq_details)
				{
					let ques = (<div className="card">
							<div className="card-header p-0" id={"heading"+j} >
							  <h2 className="mb-0">
								<button className="btn btn-link fullclick" type="button" data-toggle="collapse" data-target={"#collapse"+j} aria-expanded="true" aria-controls={"#collapse"+j} >
								{faq_details[j].ques}
								</button>
							  </h2>
							</div>

							<div id={"#collapse"+j} className="collapse show" aria-labelledby={"heading"+j} data-parent="#accordionExample">
							  <div className="card-body">
								<p>{faq_details[j].answer}</p>
							  </div>
							</div>
						 </div>)
						ques_arr.push(ques);
					j++;
				}
				setFaq_content(ques_arr);
			}
		
		})
		.catch(err => {
			console.log(err)
		})
		
	  });
	
	
	const [email, setEmail] = useState({initialState:''});
	const [UName, setUName] = useState({initialState:''});
	const [Msg, setMsg] = useState({initialState:''});
	const [redirect, setRedirect] = useState(false);
	
	const handleSubmit = async (e:SyntheticEvent) => {
		e.preventDefault();
		//console.log('works');
	const data = {
		email: email,
		UName: UName,
		Msg: Msg
	}
	
	
	axios.post('submit_query', data)
		.then(res => {
			
			if(res.data.user_data !=='')
			{
				console.log(res.data.user_data);
				//localStorage.clear();
				//localStorage.setItem('auth_token', res.data.token);
				//axios.defaults.headers.common['Authorization'] = res.data.token;
				//console.log(localStorage.getItem('auth_token'));
				setSuccess("Thank you for contacting us");
				//const content = res.data;
				setRedirect(true);
				//props.setName(content.name);
				
			}else{
				//const err_msg = res.data.message;
				setError("Please try again later");
				console.log(res.data);
			}
		
		})
		.catch(err => {
			console.log(err)
		})
		
		
	};
	
	if (redirect) {
        return <Redirect to="/questions"/>;
    }
  
	if (redirect2) {
        return <Redirect to="/subscriptions"/>;
    }
	
	return (
	<>
	<Navbar />
	<section className="smallbnr">
	<div className="inner-bnr" style={{backgroundImage:`url(${Banner_Image})`}} >
			<div className="container textcntr">
				<div className="row justify-content-md-center">
					<div className="col-lg-8">
						<div className="small_bnrhd"> Question </div>
						<div className="small-bnrtxt">{Banner_Content}</div>
					</div>
				</div>
			</div>
		</div>
	</section>

	<section id="about-investro">
		<div className="container">
			<div className="row">
				<div className="col-lg-12">
					<div className="investrobox">
						<div className="webhd-withline">Frequently asked questions</div>
						<p>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</p>
					</div>
				</div>

				<div className="col-lg-12">
					<div className="Faqbox">
						<div className="accordion" id="accordionExample">
						{Faq_content}
						</div>
					</div>
				</div>
				
			</div>
		</div>
	</section>

	<section id="drop-us">
		<div className="container">
			<div className="row">
				<div className="col-lg-12">
					<div className="webhd-withline">Frequently asked questions</div>
					<p>{Contact_Middle_heading}</p>
					<div className="dropform">
					<div className="success_msg"> {Success_msg}</div>
					<div className="error_msg"> {Error_msg}</div>
						<form onSubmit={handleSubmit} >
							  <div className="row">
								<div className="col-lg-6 mb-3">
								  <input type="text" className="form-control" placeholder="Name" onChange={e => setUName(e.target.value)} />
								</div>
								<div className="col-lg-6 mb-3">
								  <input type="text" className="form-control" placeholder="Email address..." onChange={e => setEmail(e.target.value)} />
								</div>
								<div className="col-lg-12">
									<div className="form-group">
										<textarea className="form-control" id="" rows="5" placeholder="Type here..." onChange={e => setMsg(e.target.value)} ></textarea>
									  </div>
								</div>
								<button className="snd-btn mb-5">Send</button>
							  </div>
							</form>
					</div>
				</div>
			</div>
		</div>
	</section>
		<section className="mail-sbe-sec">
			<div className="container">
				<button className="blog-suscribe mt-5" onClick ={()=>setRedirect2(true)} >Subscribe</button>
			</div>
		</section>
	<Footer />
	</>
	);
};

export default Questions;
