import React, { useState} from 'react';
import { Redirect} from "react-router-dom";
import StripeCheckout from 'react-stripe-checkout';
import axios from 'axios';

const StripeCheckoutButton = ({ price }) => {
    const priceForStripe = price * 100;
    const publishableKey = 'pk_test_51ItOmVSDypQBMUuymdL3WRVzXJnLs7efMMAyx87wq8JvxI8U0hUmxk68cnwetkplK5USFEZupaM2eaDczjjybNOG00u1IzCDs4';
	const [Error_msg, setError] = useState("");
	const [Success_msg, setSuccess] = useState("");
	const [redirect, setRedirect] = useState(false);
	
    const onToken = token => {
        console.log(token);
		const data = {
			amount: priceForStripe,
			email: token.email,
			token: token.id
		}
		console.log(data);
		axios.post('payment', data)
		.then(res => {
			
			if(res.data.userid !=='')
			{
				setSuccess(res.data.message);
				setRedirect(true);
				//window.location.reload();
				
			}else{
				setError(res.data.message);
			}
		
		})
		.catch(err => {
			console.log(err)
		})
			
       //alert('Payment Succesful!');
    };
	
	if (redirect) {
		
		return <Redirect to="/subscriptions"/>;
	}
		
    return (
	<>
		<div className="success_msg"> {Success_msg}</div>
			<div className="error_msg"> {Error_msg}</div>	
			<StripeCheckout
				label='Pay Now'
				billingAddress
				shippingAddress
				image='https://www.freakyjolly.com/wp-content/uploads/2020/04/fj-logo.png'
				description={`Your total is $${price}`}
				amount={priceForStripe}
				panelLabel='Pay Now'
				token={onToken}
				stripeKey={publishableKey}
			/>
		</>
    )
}

export default StripeCheckoutButton;

