import React, {SyntheticEvent, useState} from 'react';
import { NavLink , Redirect} from "react-router-dom";
import logo from './images/logo.png';
import close_img from './images/close.png';
import Edit from './images/Edit.png';
import avatar from './images/avatar.jpg';
import dummy_prof from './images/dummy-prof.jpg';
//import "../node_modules/bootstrap/dist/css/classic.css";

const Dashboard = (props: { name: string, setName: (name: string) => void }) => {
	
	const [redirect, setRedirect] = useState(false);
	const logout = async () => {
        await fetch('http://localhost:8000/api/logout', {
            method: 'POST',
            headers: {'Content-Type': 'application/json'},
            credentials: 'include',
        });

        props.setName('');
    }
	
    if (props.name === '') {
		
		setRedirect(true);
    }
	
	if (redirect) {
        return <Redirect to="/login"/>;
    }
	
	return (
	<>
	<div className="wrapper">
		<nav id="sidebar" className="sidebar">
			<div className="sidebar-content ">
			<NavLink className="sidebar-brand" to="/dashboard">
          		<span className="align-middle"><img src={logo} alt="logo" className="img-responsive" /></span>
        	</NavLink>

				<ul className="sidebar-nav">
					<li className="sidebar-item active">
						<NavLink to="/dashboard" className="sidebar-link">
			             <span className="align-middle">Profile</span>
			            </NavLink>
					</li>
					<li className="sidebar-item">
						<NavLink to="/previous_newsletters" className="sidebar-link">
              				<span className="align-middle"> Previous Newsletters </span>
            			</NavLink>
					</li>
					<li className="sidebar-item">
						<NavLink to="/trade_updates" className="sidebar-link">
              				<span className="align-middle"> Trade Updates </span>
            			</NavLink>
					</li>
					<li className="sidebar-item">
						<NavLink to="/special_reports" className="sidebar-link">
              				<span className="align-middle"> Special Reports </span>
            			</NavLink>
					</li>
					
				</ul>
				
			</div>
		</nav>

		<div className="main">
			<nav className="navbar navbar-expand navbar-light bg-white">
				<NavLink className="sidebar-toggle d-flex mr-2" to="/dashboard">
			          <i className="hamburger align-self-center"></i>
			        </NavLink>
			        <span className="welcome">Welcome to Investoroo</span>

				<div className="navbar-collapse collapse ">
				<form className="form-inline ml-auto top-srch">
					<input className="form-control form-control-no-border mr-sm-2" type="text" placeholder="Search" aria-label="Search" />
					<i className="align-middle mr-2 fas fa-fw fa-search"></i>
				</form>
					<ul className="navbar-nav mobile-ml-auto">
						<li className="nav-item dropdown">
							<NavLink className="nav-link dropdown-toggle d-none d-sm-inline-block" to="/" data-toggle="dropdown">
				                <img src={avatar} className="avatar img-fluid rounded-circle mr-1" alt="Chris Wood" /> <span className="text-dark">Chris Wood</span>
				              </NavLink>
							<div className="dropdown-menu dropdown-menu-right">
								<NavLink className="dropdown-item" data-toggle="modal" data-target="#exampleModal" to="/pages_profile"><i className="align-middle mr-1" data-feather="user"></i> Profile</NavLink>
								<NavLink className="dropdown-item" to="/">Sign out</NavLink>
							</div>
						</li>
					</ul>
				</div>
			</nav>
			
			<div className="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
				  <div className="modal-dialog" role="document">
				    <div className="modal-content">
				      <div className="modal-header-profile">
				        <h5 className="modal-title-pop" id="exampleModalLabel">Profile</h5>
				        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
				          <span aria-hidden="true"><img src={close_img} alt="cross icon" /></span>
				        </button>
				      </div>
				      <div className="modal-body pt-0">
				      	<button className="editbox"><i className="align-middle mr-2 fas fa-fw fa-edit"></i> Edit</button>
							<div className="upload-box posi-relative">
							     <div className="circle circl-new">
									<img className="profile-pic profile-pic-new" src={dummy_prof} alt="profile" />
							     </div>
							     <div className="p-image p-image-new">
							     	<img src={Edit} alt="icon" className="upload-button" />
							        <input className="file-upload" type="file" accept="image/*" />
							     </div>
							</div>
						<div className="profile-form-popup">
								<form>
								  <div className="row">
								    <div className="col-lg-6 mb-4">
								      <input type="text" className="form-control" placeholder="Name" />
								    </div>
								    <div className="col-lg-6 mb-4">
								      <input type="text" className="form-control" placeholder="Phone Number" />
								    </div>
								     <div className="col-lg-12 mb-4">
								      <input type="text" className="form-control" placeholder="Email Address" />
								    </div>
								     <div className="col-lg-6 mb-4">
								     	<label>Date of Subscription</label>
								      <input type="date" className="form-control" />
								    </div>
								    <div className="col-lg-6 mb-4">
								    	<label>Expiry Date of Subscription</label>
								      <input type="date" className="form-control" />
								    </div>
								    <div className="col-lg-12">
								    	<div className="form-group">
										    <textarea className="form-control" id="exampleFormControlTextarea1" rows="4" placeholder="Billing Address"></textarea>
										  </div>
								    </div>
								    <div className="col-lg-12 text-center">
								    	<button className="btn logout-modalbtn"> Logout</button>
								    </div>
								  </div>
								</form>
							</div>
				      </div>
				    </div>
				  </div>
				</div>
				
			<main className="content">
				<div className="container-fluid p-0">
					<div className="row">
						<div className="col-lg-12 mb-5">
							<div className="cont-tabhd">Dashboard</div>
						</div>
						<div className="col-lg-11">
							<div className="profile-form-box">
								<form>
								  <div className="row">
								    <div className="col-lg-6 mb-4">
								      <input type="text" className="form-control" placeholder="Name" />
								    </div>
								    <div className="col-lg-6 mb-4">
								      <input type="text" className="form-control" placeholder="Phone Number" />
								    </div>
								     <div className="col-lg-12 mb-4">
								      <input type="text" className="form-control" placeholder="Email Address" />
								    </div>
								     <div className="col-lg-6 mb-4">
								     	<label>Date of Subscription</label>
								      <input type="date" className="form-control" />
								    </div>
								    <div className="col-lg-6 mb-4">
								    	<label>Expiry Date of Subscription</label>
								      <input type="date" className="form-control" />
								    </div>
								    <div className="col-lg-12">
								    	<div className="form-group">
										    <textarea className="form-control" id="exampleFormControlTextarea1" rows="3" placeholder="Billing Address"></textarea>
										  </div>
								    </div>
								    <div className="col-lg-12 text-center">
								    	<button className="logout-pro"> Logout</button>
								    </div>
								  </div>
								</form>
							</div>
						</div>
					</div>
				</div>
			</main>
	</div>
	</div>
	</>
	);
};

export default Dashboard;
