import React, {useState, useEffect} from 'react';
import { NavLink , Redirect} from "react-router-dom";
import blog_banner from './images/blog-banner.png';
import blog_img1 from './images/blog-img-1.png';
import blog_thumb1 from './images/blog-thumb-1.png';
import blog_thumb2 from './images/blog-thumb-2.png';
import search from './images/search.png';
import closeimg from './images/close.png';
import Navbar from './Navbar';
import Footer from './Footer';
import axios from 'axios';
import ReactPaginate from 'react-paginate';
import './App.css';

const Blog = () => {
	
	const [Banner_Image, setBanner_Image] = useState("");
	const [Banner_Content , setBanner_Content ] = useState("");
	const [Contact_Middle_heading, setContact_Middle_heading] = useState("");
	const [Subscribe_Now_Content, setSubscribe_Now_Content] = useState("");
	const [Blog_list, setBlog_list] = useState("");
	const [Search_keywords, setSearch_keywords] = useState("");
	const [Recent, setRecent] = useState(0);
	const [Popular, setPopular] = useState(0);
	const [offset, setOffset] = useState(0);
	const [Current, setCurrent] = useState(0);
	const [data, setData] = useState([]);
	const [perPage] = useState(3);
	const [pageCount, setPageCount] = useState(0)
	const [Business_total, setBusiness_total] = useState(0)
	const [Stock_total, setStock_total] = useState(0)
	const [copied, setCopied] = useState(false);
	let textarea ='';
	const [base_url, seturl] = useState(false);
	seturl(window.location.href);
	//const { state } = this.props.history.location;
	//{localStorage.removeItem("BlogId")}
	//{localStorage.setItem("BlogId", pd.id)}
	  useEffect(() => {
		
		axios.post('blog')
		.then(res => {
			
			if(res.data.page_data !=='')
			{
				//console.log(res.data.blog_details);
				let pageData = res.data.page_data;
				let recent_post = res.data.recent_post;
				let popular_post = res.data.popular_post;
				setBusiness_total(res.data.business_count);
				setStock_total(res.data.stock_count);
				let i =0;
				for(i in pageData)
				{
					if(pageData[i].section_name === 'Banner_Image')
					{
						 setBanner_Image(pageData[i].content_image);
						 
					}else if(pageData[i].section_name === 'Banner_Content ')
					{
						setBanner_Content (pageData[i].content);
					}else if(pageData[i].section_name === 'Subscribe_Now_Content')
					{
						setSubscribe_Now_Content(pageData[i].content);
					}
					i++;
				}
				
				let j =0;
				let arr =[];
				for(j in recent_post)
				{
					let list = (<li className="media">
								<img src={recent_post[j].blog_img} className="mr-3" height="75px" alt="images" />
								<div className="media-body">
								  <h5 className="mt-0 mb-2">{recent_post[j].title}</h5>
								 <div className="thumb-date"><span className="blog-date"> {recent_post[j].created_at} </span></div>
								</div>
							  </li>)
					arr.push(list);
					j++;
				}
				setRecent(arr);
				
				
				let k =0;
				let arr1 =[];
				for(k in popular_post)
				{
					let list = (<li className="media">
								<img src={popular_post[k].blog_img} className="mr-3" height="75px" alt="images" />
								<div className="media-body">
								  <h5 className="mt-0 mb-2">{popular_post[k].title}</h5>
								 <div className="thumb-date"><span className="blog-date"> {popular_post[k].created_at} </span></div>
								</div>
							  </li>)
					arr1.push(list);
					j++;
				}
				setPopular(arr1);
						  
				const data = res.data.blog_details;
				//console.log(offset);
				//console.log(perPage);
				//console.log(offset + perPage);
               const slice = data.slice(offset, offset + perPage);
			   //console.log(slice);
                const postData = slice.map(pd => <div key={pd.id}>
                    <div className="blog-box wow fadeInUp">
						<div className="card bord-rad border-0">
						  <img src={pd.blog_img} className="card-img-top" alt="team images" />
						  <div className="card-body blog-style">
							<span className="admin-style">
								<span className="admin-icon"> Admin </span><span className="blog-date">{pd.created_at} </span></span>
									<span className="share" data-toggle="modal" data-target="#share"><i className="fas fa-share-alt"></i></span>
									
							<h3> {pd.title}</h3>
							<p>{pd.short_desc}</p>
								
							<button onClick={()=>setBlogid(pd.blog_id)} className="read-more">Read More </button>
						  </div>
						</div>
					</div>
                    <img src={pd.thumbnailUrl} alt=""/>
                </div>)
                setData(postData)
				
                setPageCount(Math.ceil(data.length / perPage))	
			}
		
		})
		.catch(err => {
			console.log(err)
		})
		
	  },[offset]);
		
		
	const handlePageClick = (e) => {
		const selectedPage = e.selected;
		setOffset(selectedPage + 1);
		//setCurrent(selectedPage + 1);
	};
	
	const handleSearch = (e) => {
		localStorage.removeItem('keywords');
		localStorage.setItem('keywords', Search_keywords);
		setRedirect1(true);
	};

		const [email, setEmail] = useState({initialState:''});
		const [username, setUsername] = useState({initialState:''});
		const [BlogId, setBlogId] = useState(0);
		const [Search, setSearch] = useState(0);
		const [redirect, setRedirect] = useState(false);
		const [redirect1, setRedirect1] = useState(false);
		const [redirect2, setRedirect2] = useState(false);
		
		function setBlogid(blogid) {
			localStorage.removeItem('BlogId');
			localStorage.setItem('BlogId', blogid);
			setRedirect(true);
			//alert(`hello, ${blogid}`);
		  }
  
	if (redirect) {
        return <Redirect to="/blogDetails"/>;
    }
	
	if (redirect1) {
        return <Redirect to="/blogSearch"/>;
    }
	
	if (redirect2) {
        return <Redirect to="/subscriptions"/>;
    }
	

	return (
	<>
	<Navbar />
	<section className="smallbnr">
		<div className="inner-bnr" style={{backgroundImage:`url(${Banner_Image})`}} >
		<div className="container textcntr">
			<div className="row justify-content-md-center">
				<div className="col-lg-8">
					<div className="small_bnrhd"> Blog </div>
					<div className="small-bnrtxt">News + Updates</div>
				</div>
			</div>
		</div>
	</div>
	</section>

	<section id="blog-sec">
		<div className="container">
			<div className="row">
				<div className="col-lg-8">
				{data}
				</div>
				<div className="col-lg-4">
					<div className="thumb-box">
						<div className="serarch-thumb mb-5">
							<form onSubmit={handleSearch} >
							  <input type="text" className="form-control blog-search" placeholder="Search" onChange={e => setSearch_keywords(e.target.value)} />
							  <button type="submit" ><img src={search} alt="images" /></button>
							</form>
						</div>
						<div className="blog-cat-box">
							<div className="blog-cag-hd">Categories</div>
							<div className="catlist">
								<li>Business <span>({Business_total})</span></li>
								<li>Stock Market <span>({Stock_total})</span></li>
							</div>
						</div>
						<div className="recent-box">
							<div className="blog-cag-hd">Recent Posts</div>
							<ul className="list-unstyled">
							{Recent}
							</ul>
						</div>
						<div className="mostpopular-box mt-4">
							<div className="blog-cag-hd">Most Popular</div>
							<ul className="list-unstyled">
							{Popular}
							</ul>
						</div>
					</div>
				</div>

				<div className="col-lg-12">
				
				<ReactPaginate
                    previousLabel={"prev"}
                    nextLabel={"next"}
                    breakLabel={"..."}
                    breakClassName={"break-me"}
                    pageCount={pageCount}
                    marginPagesDisplayed={3}
                    pageRangeDisplayed={5}
                    onPageChange={handlePageClick}
                    containerClassName={"pagination"}
                    subContainerClassName={"pages pagination"}
                    activeClassName={"active"}/>
					<button className="blog-suscribe mt-5" onClick ={()=>setRedirect2(true)} >Subscribe</button>
				</div>

			</div>
		</div>
	</section>
	
	<div className="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
	  <div className="modal-dialog" role="document">
		<div className="modal-content">
		  <div className="modal-header-new">
			<button type="button" className="close" data-dismiss="modal" aria-label="Close">
			  <span aria-hidden="true"><img src={closeimg} alt="cross icon" /></span>
			</button>
		  </div>
		  <div className="modal-body">
			<div className="subsc-popbox">
				<h2>Subscribe for latest Updates</h2>
				<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.</p>
				<div className="subpop-form">
					<form>
					  <div className="row">
						<div className="col-lg-12 mb-3">
						  <input type="text" className="form-control" placeholder="Name" onChange={e => setUsername(e.target.value)} />
						</div>
						<div className="col-lg-12 mb-3">
						  <input type="text" className="form-control" placeholder="Email" onChange={e => setEmail(e.target.value)} />
						</div>
						
						<button className="subsc-btn">Subscribe Now</button>
					  </div>
					</form>
				</div>
			</div>
		  </div>
		</div>
	  </div>
	</div>
	
	<div className="modal fade" id="share" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
	  <div className="modal-dialog" role="document">
		<div className="modal-content">
		  <div className="modal-header-new">
			<button type="button" className="close" data-dismiss="modal" aria-label="Close">
			  <span aria-hidden="true"><img src={closeimg} alt="cross icon" /></span>
			</button>
		  </div>
		  <div className="modal-body">
			<div className="subsc-popbox">
				<h2>Share Blog Link</h2>
				<div className="subpop-form">
				<button onClick={() =>  navigator.clipboard.writeText({base_url})} >
				Copy</button>
				</div>
			</div>
		  </div>
		</div>
	  </div>
	</div>
	
	
	<Footer />
	</>
	);
};

export default Blog;
