import React from 'react';
import { NavLink } from "react-router-dom";
import logo from './images/logo.png';
//import './App.css';

const Navbar = () => {
	return (
	<>
	<div className="full-top">
	<div className="top-part">
		<div className="container">
			<div className="row">
				<div className="col-lg-12">
					<div className="topsocial">
						<ul>
							<li><NavLink to="https://www.facebook.com"><i className="fab fa-facebook-f"></i></NavLink></li>
							<li><NavLink to="https://www.twitter.com"><i className="fab fa-twitter"></i></NavLink></li>
							<li><NavLink to="https://www.linkedin.com"><i className="fab fa-linkedin-in"></i></NavLink></li>
						</ul>
					</div>
				</div>
			</div>
		</div>
	</div>
	<div className="container">
			<div className="row">
				<div className="col-lg-12">
					<div className="navbar-box">
						<nav className="navbar navbar-expand-lg navbar-light bg-light navmob">
						  <NavLink className="navbar-brand" to="/"><img src={logo} alt="logo" /></NavLink>
						  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
							<span className="navbar-toggler-icon"></span>
						  </button>

						  <div className="collapse navbar-collapse" id="navbarSupportedContent">
							<ul className="navbar-nav ml-auto">
							  <li className="nav-item active">
								<NavLink className="nav-link" to="/"> Home <span className="sr-only">(current)</span></NavLink>
							  </li>
							  <li className="nav-item"><NavLink className="nav-link" to="/about">About</NavLink></li>
							  <li className="nav-item"><NavLink className="nav-link" to="/subscriptions">Subscriptions</NavLink></li>
							  <li className="nav-item"><NavLink className="nav-link" to="/blog">Blog</NavLink></li>
							  <li className="nav-item"><NavLink className="nav-link" to="/questions">Questions</NavLink></li>
							   <li className="nav-item"><NavLink className="nav-link login" to="/login">Login</NavLink></li>
							</ul>
						  </div>
						</nav>
					</div>
				</div>
			</div>
		</div>
	</div>
	</>
	);
};

export default Navbar;
