import React, { useState, useEffect} from 'react';
import { NavLink , Redirect} from "react-router-dom";
import subscriptions_banner from './images/subscriptions-banner.png';
import top_secret from './images/top-secret.png';
import reader from './images/reader.png';
import quotes from './images/quotes.png';
import closeimg from './images/close.png';
import Navbar from './Navbar';
import Footer from './Footer';
import axios from 'axios';
import './App.css';
//import StripeContainer from './components/StripeContainer';

//import './App.css';

const Subscriptions = () => {
	
	//const [showItem, setShowItem] = useState(false);
	const [Subscription_Banner_Image, setSubscription_Banner_Image] = useState("");
	const [Subscription_Top_Heading_Content, setSubscription_Top_Heading_Content] = useState("");
	const [Subscription_Middle_Heading_Content, setSubscription_Middle_Heading_Content] = useState("");
	const [Subscription_Middle_Content, setSubscription_Middle_Content] = useState("");
	const [Subscription_Regular_Updates, setSubscription_Regular_Updates] = useState("");
	const [Subscription_Expert_Analysis, setSubscription_Expert_Analysis] = useState("");
	const [Subscription_Professional_Advice, setSubscription_Professional_Advice] = useState("");
	const [Subscription_Urgent_News, setSubscription_Urgent_News] = useState("");
	const [Subscription_Financial_Literacy, setSubscription_Financial_Literacy] = useState("");
	const [Subscription_Join_Community, setSubscription_Join_Community] = useState("");
	const [Base_Plan, setBase_Plan] = useState("");
	const [Advanced_Plan, setAdvanced_Plan] = useState("");
	const [Advanced_Plan_Price, setAdvanced_Plan_Price] = useState("");
	const [Subscribe_Now_Content, setSubscribe_Now_Content] = useState("");
	const [Error_msg, setError] = useState("");
	const [Success_msg, setSuccess] = useState("");
	const [email, setEmail] = useState("");
	const [username, setUsername] = useState("");
	const [Testimonial, setTestimonial] = useState(0);
	const [Lower_slider, setLower_slider] = useState(0);
	
	  useEffect(() => {
		axios.post('subscription')
		.then(res => {
			
			if(res.data.page_data !=='')
			{
				//console.log(res.data.page_data);
				let pageData = res.data.page_data;
				let subscription_plan = res.data.subscription_plan;
				//console.log(subscription_plan);
				
				let testimonial = res.data.testimonial_content;
				
				let h =0;
				let arr12 =[];
				let slider =[];
				
				
				//console.log(testimonial);
				for(h in testimonial)
				{
					
					if(h ==2)
					{
						let list = (<div className="carousel-item active">
							  <p>{testimonial[h].msg}</p>
							  <div className="reader-name">{testimonial[h].name}</div>
							  <span className="reader-img mobile-view"><img src={testimonial[h].img} alt="images"/></span>
							</div>)
							
							let sli = (<li data-target="#carouselExampleIndicators" data-slide-to={h} className="active"><span className="reader-img"><img src={testimonial[h].img} alt="images"/></span>
							</li>)
							
						arr12.push(list);
						slider.push(sli);
						
					}else{
						let list = (<div className="carousel-item">
							  <p>{testimonial[h].msg}</p>
							  <div className="reader-name">{testimonial[h].name}</div>
							  <span className="reader-img mobile-view"><img src={testimonial[h].img} alt="images"/></span>
							</div>)
							
							let sli = (<li data-target="#carouselExampleIndicators" data-slide-to={h}><span className="reader-img"><img src={testimonial[h].img} alt="images"/></span>
							</li>)
							
						arr12.push(list);
						slider.push(sli);
						
					}
					h++;
				}
				//console.log(arr12);
				//console.log(slider);
				setTestimonial(arr12);
				setLower_slider(slider);
				
				let j =0;
				for(j in subscription_plan)
				{
					if(subscription_plan[j].plan_name === 'Basic')
					{
						let response = subscription_plan[j].plan_description.split(',');
						let i =0;
						let arr =[];
						 for(i in response)
						 {
							 let li = (<li>{response[i]}</li>)
							 arr.push(li);
							 i++;
						 }
						let base_desc =( <ul className="sub-doc">
						{arr}
						</ul>)
						
						
						setBase_Plan(base_desc);
						
						
					}else if(subscription_plan[j].plan_name === 'Advanced'){
						
						let response = subscription_plan[j].plan_description.split(',');
						let i =0;
						let arr =[];
						 for(i in response)
						 {
							 let li = (<li>{response[i]}</li>)
							 arr.push(li);
							 i++;
						 }
						let base_desc =( <ul className="sub-doc">
						{arr}
						</ul>)
						
						setAdvanced_Plan(base_desc);
						setAdvanced_Plan_Price(subscription_plan[j].plan_price);
					}
					j++;
				}
				let i =0;
				for(i in pageData)
				{
					//console.log(pageData[i]);
					if(pageData[i].section_name === 'Subscription_Banner_Image')
					{
						 setSubscription_Banner_Image(pageData[i].content_image);
						 
					}else if(pageData[i].section_name === 'Subscription_Top_Heading_Content')
					{
						setSubscription_Top_Heading_Content(pageData[i].content);
					}else if(pageData[i].section_name === 'Subscription_Middle_Heading_Content')
					{
						setSubscription_Middle_Heading_Content(pageData[i].content);
					}else if(pageData[i].section_name === 'Subscription_Middle_Content')
					{
						setSubscription_Middle_Content(pageData[i].content);
					}else if(pageData[i].section_name === 'Subscription_Regular_Updates')
					{
						setSubscription_Regular_Updates(pageData[i].content);
					}else if(pageData[i].section_name === 'Subscription_Expert_Analysis')
					{
						setSubscription_Expert_Analysis(pageData[i].content);
					}else if(pageData[i].section_name === 'Subscription_Professional_Advice')
					{
						setSubscription_Professional_Advice(pageData[i].content);
					}else if(pageData[i].section_name === 'Subscription_Urgent_News')
					{
						setSubscription_Urgent_News(pageData[i].content);
					}else if(pageData[i].section_name === 'Subscription_Financial_Literacy')
					{
						setSubscription_Financial_Literacy(pageData[i].content);
					}else if(pageData[i].section_name === 'Subscription_Join_Community')
					{
						setSubscription_Join_Community(pageData[i].content);
					}else if(pageData[i].section_name === 'Subscribe_Now_Content')
					{
						setSubscribe_Now_Content(pageData[i].content);
					}
					i++;
				}
			}
		
		})
		.catch(err => {
			console.log(err)
		})
		
	  });
	
	const Freesubscribe = async (e:SyntheticEvent) => {
		e.preventDefault();
		//console.log('works');
		const data = {
			email: email,
			name: username
		}
		console.log(data);
		axios.post('freeuser', data)
		.then(res => {
			
			if(res.data.userid !=='')
			{
				setSuccess("User subscribed");
				window.location.reload();
				
			}else{
				setError("Subscription fail");
			}
		
		})
		.catch(err => {
			console.log(err)
		})
		
	};
	
	return (
	<>
	<Navbar />
	<section className="smallbnr">
		<div className="inner-bnr" style={{backgroundImage:`url(${Subscription_Banner_Image})`}} >
		<div className="container textcntr">
			<div className="row justify-content-md-center">
				<div className="col-lg-8">
					<div className="small_bnrhd"> Subscriptions </div>
				</div>
			</div>
		</div>
	</div>
	</section>

	<section id="about-investro">
		<div className="container">
			<div className="row">
				<div className="col-lg-12">
					<div className="investrobox">
						<div className="webhd-withline">Our Subscription</div>
						<p>{Subscription_Top_Heading_Content}</p>
					</div>
				</div>
			</div>
			{ /* <div className="App">
      <h1>Investoroo Advanced Subscription</h1>
      {showItem ? <StripeContainer/> : <> <h3>${Advanced_Plan_Price}.00</h3>
      <button onClick={() => setShowItem(true)}>Subscribe</button></>}
			</div> */}
	
		<div className="row">
				<div className="col-lg-4 align-self-center mb-3">
					<div className="acquire">Acquire your Investment goals</div>
				</div>
				<div className="col-lg-4 mb-3">
					<div className="price-box">
						<h2>Basic</h2>
						<h1>Free Forever</h1>
						<p>{Base_Plan}</p>
							{/* <ul className="sub-doc">
							<li>Blogs  <NavLink to="/"> See Sample </NavLink></li>
							<li>Email Updates <NavLink to="/"> See Sample </NavLink></li>
							</ul> */}
						<a className="btn subsc-now" data-toggle="modal" data-target="#subModal" role="button"> Subscribe Now </a>
					</div>
				</div>
				<div className="col-lg-4 mb-3">
					<div className="price-box">
						<h2>Advance</h2>
						<h1>$ {Advanced_Plan_Price}/year</h1>
						<p>{Advanced_Plan}</p>
							{/* <ul className="sub-doc">
							<li>Everything in Free +  <NavLink to="/"> See Sample </NavLink></li>
							<li>Dashboard Access <NavLink to="/"> See Sample </NavLink></li>
							<span>(Access to all content)</span>
							<li> Newsletters <NavLink to="/"> See Sample </NavLink></li>
							<li> Trade Update  <NavLink to="/"> See Sample </NavLink></li>
							<li> Special Reports <NavLink to="/"> See Sample </NavLink></li>
							</ul> */}
						<NavLink className="btn subsc-now" to="/checkout" role="button"> Subscribe Now </NavLink>
					</div>
				</div>
			</div>

		</div>
	</section>

	<section id="knowledge-help">
		<div className="container">
			<div className="row">
				<div className="col-lg-12 mb-5">
					<div className="webhd-withline">Knowledge helps you stand out</div>
					<p>{Subscription_Middle_Content}</p>
				</div>
				<div className="col-lg-4">
					<div className="tradebox">
						<div className="trade-icon"><img src={top_secret} alt="images" /></div>
						<h2>Regular Updates</h2>
						<p>{Subscription_Regular_Updates}</p>
					</div>
				</div>
				<div className="col-lg-4">
					<div className="tradebox">
						<div className="trade-icon"><img src={top_secret} alt="images" /></div>
						<h2>Expert Analysis</h2>
						<p>{Subscription_Expert_Analysis}</p>
					</div>
				</div>
				<div className="col-lg-4">
					<div className="tradebox">
						<div className="trade-icon"><img src={top_secret} alt="images" /></div>
						<h2>Professional Advice</h2>
						<p>{Subscription_Professional_Advice}</p>
					</div>
				</div>
				<div className="col-lg-4">
					<div className="tradebox">
						<div className="trade-icon"><img src={top_secret} alt="images" /></div>
						<h2>Urgent_News</h2>
						<p>{Subscription_Urgent_News}</p>
					</div>
				</div>
				<div className="col-lg-4">
					<div className="tradebox">
						<div className="trade-icon"><img src={top_secret} alt="images" /></div>
						<h2>Financial Literacy</h2>
						<p>{Subscription_Financial_Literacy}</p>
					</div>
				</div>
				<div className="col-lg-4">
					<div className="tradebox">
						<div className="trade-icon"><img src={top_secret} alt="images" /></div>
						<h2>Join Community</h2>
						<p>{Subscription_Join_Community}</p>
					</div>
				</div>
			</div>
		</div>
	</section>
		<section className="readersay-sec" style={{position: 'relative'}}>
			<div className="container">
				<div className="row justify-content-center">
					<div className="col-lg-9 webhd">What our readers say</div>
				</div>
				<div className="row justify-content-center">
					<div className="col-lg-9">
						<span className="quotes-box"><img src={quotes} alt="images" className="img-fluid"/></span>
						<div id="carouselExampleIndicators" className="carousel slide" data-ride="carousel">
						 
						  <div className="carousel-inner">
						  {Testimonial}
						  </div>
						  <ol className="carousel-indicators">
						  {Lower_slider}
						  </ol>
						  
						</div>
					</div>
				</div>
			</div>
		</section>
	
	<div className="modal fade" id="subModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
	  <div className="modal-dialog" role="document">
		<div className="modal-content">
		  <div className="modal-header-new">
			<button type="button" className="close" data-dismiss="modal" aria-label="Close">
			  <span aria-hidden="true"><img src={closeimg} alt="cross icon" /></span>
			</button>
		  </div>
		  <div className="modal-body">
			<div className="subsc-popbox">
				<h2>Subscribe for latest Updates</h2>
				<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt.</p>
				<div className="success_msg"> {Success_msg}</div>
				<div className="error_msg"> {Error_msg}</div>
				<div className="subpop-form">
					<form onSubmit={Freesubscribe} >
					  <div className="row">
						<div className="col-lg-12 mb-3">
						  <input type="text" className="form-control" placeholder="Name" onChange={e => setUsername(e.target.value)} />
						</div>
						<div className="col-lg-12 mb-3">
						  <input type="text" className="form-control" placeholder="Email" onChange={e => setEmail(e.target.value)} />
						</div>
						
						<button className="subsc-btn" >Subscribe Now</button>
					  </div>
					</form>
				</div>
			</div>
		  </div>
		</div>
	  </div>
	</div>
	
	<Footer />
	</>
	);
};

export default Subscriptions;
