import React, {useState, useEffect} from 'react';
import { NavLink } from "react-router-dom";
//import logo from './logo.svg';
//import './App.css';
import axios from 'axios';

const Footer = () => {
	
	const [Footer_Left_Side_Content, setFooter_Left_Side_Content] = useState("");
	const [Footer_Right_Side_Content , setFooter_Right_Side_Content] = useState("");
	
	  useEffect(() => {
		axios.post('footer')
		.then(res => {
			
			if(res.data.page_data !=='')
			{
				//console.log(res.data.page_data);
				let pageData = res.data.page_data;
				let i =0;
				for(i in pageData)
				{
					//console.log(pageData[i]);
					if(pageData[i].section_name === 'Footer_Left_Side_Content')
					{
						 setFooter_Left_Side_Content(pageData[i].content);
						 
					}else if(pageData[i].section_name === 'Footer_Right_Side_Content')
					{
						//console.log(pageData[i].content);
						setFooter_Right_Side_Content(pageData[i].content);
					}
					i++;
				}
			}
		
		})
		.catch(err => {
			console.log(err)
		})
		
	  });
	  
	return (
		<>
		<section className="footer">
			<div className="container">
				<div className="row">
					<div className="col-lg-4">
						<div className="footerbox">
							<h1>Investoroo.com</h1>
							<p>{Footer_Left_Side_Content}</p>
						</div>
					</div>
					<div className="col-lg-4">
						<div className="quicklinks">
							<h4>Quick Link</h4>
							<ul>
								<li><NavLink to="/">Home</NavLink></li>
								<li><NavLink to="/about">About</NavLink></li>
								<li><NavLink to="/subscriptions">Subscriptions</NavLink></li>
								<li><NavLink to="/blog">Blog</NavLink></li>
								<li><NavLink to="/questions">Questions</NavLink></li>
							</ul>
						</div>
					</div>
					<div className="col-lg-4">
						<div className="addressbox">
							<h4>Address</h4>
							<p>{Footer_Right_Side_Content}</p>
							{/* <p>Lorem Ipsum is simply dummy text of </p> 
							<p>	printing and typesetting industry.</p>
							<p> 1945 Congress Street </p>
							<p>Building 3 </p>
							<p>Portland, ME 04102</p> */} 
						</div>
						<div className="we-social">
							<h4>We are Social</h4>
							<ul>
								<li><NavLink to="/"><i className="fab fa-facebook-f"></i></NavLink></li>
								<li><NavLink to="/"><i className="fab fa-twitter"></i></NavLink></li>
								<li><NavLink to="/"><i className="fab fa-linkedin-in"></i></NavLink></li>
							</ul>
						</div>
					</div>
				</div>
			</div>
		</section>
		<div className="copyright">
			<div className="container">
				<div className="row">
					<div className="col-lg-6 col-md-6">
						<div className="copy-left">  2021 Investoroo.com  |  All rights reserved.</div>
					</div>
					<div className="col-lg-6 col-md-6">
						<div className="copy-right"><NavLink to=""> Powered by: YES IT Labs </NavLink></div>
					</div>
				</div>
			</div>
		</div>
	</>
	);
};

export default Footer;
