import React, {SyntheticEvent, useState, useEffect} from 'react';
import { NavLink , Redirect , Switch, BrowserRouter} from "react-router-dom";
import logo from './images/logo.png';
import analysis_white from './images/analysis-white.png';
import closeimg from './images/close.png';
import axios from 'axios';
import './App.css';

const Login = (props: { setName: (name: string) => void }) => {
	
	const [Box_Content, setBox_Content] = useState("");
	
	useEffect(() => {
		axios.post('contact_us')
		.then(res => {
			
			if(res.data.page_data !=='')
			{
				//console.log(res.data.page_data);
				let pageData = res.data.page_data;
				setBox_Content(pageData[0].content);
			}
		
		})
		.catch(err => {
			console.log(err)
		})
		
	  });
	  
	const [Error_msg, setError] = useState("");
	const [Success_msg, setSuccess] = useState("");
	const [email, setEmail] = useState("");
	const [pass, setPassword] = useState("");
	const [cpass, setCpassword] = useState("");
	const [username, setUsername] = useState("");
	const [otp, setOTP] = useState("");
	const [redirect, setRedirect] = useState(false);
	const [redirect1, setRedirect1] = useState(false);
	
	const handleSubmit = async (e:SyntheticEvent) => {
		e.preventDefault();
		//console.log('works');
	const data = {
		email: email,
		pass: pass
	}
	axios.post('login', data)
		.then(res => {
			
			if(res.data.user_data !=='')
			{
				localStorage.clear();
				localStorage.setItem('auth_token', res.data.token);
				axios.defaults.headers.common['Authorization'] = res.data.token;
				console.log(localStorage.getItem('auth_token'));
				setSuccess("Login Successfull");
				const content = res.data;
				setRedirect(true);
				props.setName(content.name);
				
			}else{
				const err_msg = res.data.message;
				//console.log(res.data);
				setError("Login Fail");
			}
		
		})
		.catch(err => {
			console.log(err)
		})
		
		
	};
	
	
	const handleSubmit1 = async (e:SyntheticEvent) => {
		e.preventDefault();
		//console.log('works');
		
		if(pass === cpass)
		{
			const data = {
				name: username,
				email: email,
				pass: pass
			}
			axios.post('signup', data)
				.then(res => {
					
					if(res.data.userid !=='')
					{
						//console.log(res.data.userid);
						setSuccess("Registration Successfull");
						//setRedirect2(true);
						window.location.reload();
						
					}else{
						const err_msg = res.data.message;
						//console.log(res.data);
						setError("Registration Fail");
						
					}
				
				})
				.catch(err => {
					console.log(err)
				})
		}else{
			console.log('Fail');
			setError("Password & confirm password not matched.");
		}
		
	};
	
	
	const handleSubmit2 = async (e:SyntheticEvent) => {
		e.preventDefault();
		//console.log('works');
	const data = {
		email: email
	}
	axios.post('forgot', data)
		.then(res => {
			
			if(res.data.userid !=='')
			{
				console.log(res.data);
				setSuccess("OTP sent on your email. Please check your email.");
				
			}else{
				setError("Email does not exists.");
			}
		
		})
		.catch(err => {
			console.log(err)
			
		})
		
	};
	
	const handleSubmit3 = async (e:SyntheticEvent) => {
		e.preventDefault();
		//console.log('works');
	const data = {
		email: email,
		otp: otp
	}
	
	axios.post('checkOTP', data)
		.then(res => {
			
			if(res.data.userid !=='')
			{
				//console.log(res.data);
				//setShow(true);
				localStorage.setItem('userid', res.data.userid);
				const content = res.data;
				setRedirect1(true);
				props.setName(content.name);
				setSuccess("OTP matched.");
				//window.location.reload();
				
			}else{
				setError("OTP not matched.");
			}
		
		})
		.catch(err => {
			console.log(err)
		})
		
	};
	
	
	if (redirect) {
        return <Redirect to="/dashboard"/>;
    }
	
	if (redirect1) {
        return <Redirect to="/change_pass"/>;
    }
	
	
	return (
	<>
	<div className="backto-websites">
		<div className="container">
			<div className="row">
				<div className="col-lg-12">
					<div className="backbtn"><NavLink to="/">&#8592; Back to Home </NavLink></div>
				</div>
			</div>
		</div>
	</div>
	
	<section id="login-sec">
		<div className="container">
			<div className="row justify-content-center">
				<div className="col-lg-9">
						<div className="row login-size">
							<div className="col-lg-12 logn-tp-sec">
								<img src={logo} alt="images" className="img-fluid" />
								<div className="login-top-heder">Welcome to Investoroo</div>
							</div>
							<div className="col-lg-6 analysis-login">
									<div id="login-slide" className="owl-carousel">
										<div className="item">
											<div className="analysisbox">
												<img src={analysis_white} alt="images" />
												<h3>Insightful Analysis</h3>
												<p>{Box_Content}</p>
											</div>
										</div>
									</div>
							</div>
							<div className="col-lg-6 login-col">
								<div className="success_msg"> {Success_msg}</div>
									<div className="error_msg"> {Error_msg}</div>
									<div className="loginformbox">
										<form onSubmit={handleSubmit} >
									  <div className="row">
										<div className="col-lg-12 mb-3">
											<label>User Name or Email</label>
											<input type="email" className="form-control" placeholder="Email" onChange={e => setEmail(e.target.value)} />
										</div>
										<div className="col-lg-12 mb-3">
											<label>Password</label>
											<input type="Password" className="form-control" placeholder="" onChange={e => setPassword(e.target.value)}/>
										</div>
										{/*<div className="forget signup"><NavLink to="/" data-toggle="modal" data-target="#signup"> Sign Up </NavLink></div>*/}
										<div className="forget"><NavLink to="/" data-toggle="modal" data-target="#forgot"> Forgot Password </NavLink></div>
										<div className="login-btnbox"><button className="login-whitebtn">Login</button></div>
										<div className="having">Having trouble logging in?</div>
									  </div>
									</form>
								</div>
							</div>
						</div>
				</div>
			</div>
		</div>
	</section>
	
	
	<div className="modal fade" id="signup" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
	  <div className="modal-dialog" role="document">
		<div className="modal-content">
		  <div className="modal-header-new">
			<button type="button" className="close" data-dismiss="modal" aria-label="Close">
			  <span aria-hidden="true"><img src={closeimg} alt="cross icon" /></span>
			</button>
		  </div>
		  <div className="modal-body">
			<div className="subsc-popbox">
				<h2>Sign Up Form</h2>
				<div className="success_msg"> {Success_msg}</div>
				<div className="error_msg"> {Error_msg}</div>
				<div className="subpop-form">
					<form onSubmit={handleSubmit1} >
					  <div className="row">
						<div className="col-lg-12 mb-3">
						  <input type="text" className="form-control" placeholder="Name" onChange={e => setUsername(e.target.value)} />
						</div>
						<div className="col-lg-12 mb-3">
						  <input type="text" className="form-control" placeholder="Email" onChange={e => setEmail(e.target.value)} />
						</div>
						<div className="col-lg-12 mb-3">
						  <input type="password" className="form-control" placeholder="Password" onChange={e => setPassword(e.target.value)} />
						</div>
						<div className="col-lg-12 mb-3">
						  <input type="password" className="form-control" placeholder="Confirm Password" onChange={e => setCpassword(e.target.value)} />
						</div>
						<button className="subsc-btn">Submit</button>
					  </div>
					</form>
				</div>
			</div>
		  </div>
		</div>
	  </div>
	</div>
	
	
	<div className="modal fade" id="forgot" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
	  <div className="modal-dialog" role="document">
		<div className="modal-content">
		  <div className="modal-header-new">
			<button type="button" className="close" data-dismiss="modal" aria-label="Close">
			  <span aria-hidden="true"><img src={closeimg} alt="cross icon" /></span>
			</button>
		  </div>
		  <div className="modal-body">
			<div className="subsc-popbox">
				<h2>Forgot Password</h2>
				<div className="success_msg"> {Success_msg}</div>
				<div className="error_msg"> {Error_msg}</div>
				<div className="subpop-form">
					<form onSubmit={handleSubmit2} >
					  <div className="row">
						<div className="col-lg-12 mb-3">
						  <input type="text" className="form-control" placeholder="Email" onChange={e => setEmail(e.target.value)} />
						</div>
						<button className="subsc-btn" data-toggle="modal" data-target="#otp" >Send OTP</button>
					  </div>
					</form>
				</div>
			</div>
		  </div>
		</div>
	  </div>
	</div>
	
	
	<div className="modal fade" id="otp" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
	  <div className="modal-dialog" role="document">
		<div className="modal-content">
		  <div className="modal-header-new">
			<button type="button" className="close" data-dismiss="modal" aria-label="Close">
			  <span aria-hidden="true"><img src={closeimg} alt="cross icon" /></span>
			</button>
		  </div>
		  <div className="modal-body">
			<div className="subsc-popbox">
				<h2>Forgot Password</h2>
				<div className="success_msg"> {Success_msg}</div>
				<div className="error_msg"> {Error_msg}</div>
				<div className="subpop-form">
					<form onSubmit={handleSubmit3} >
					  <div className="row">
						<div className="col-lg-12 mb-3">
						  <input type="text" className="form-control" placeholder="OTP" onChange={e => setOTP(e.target.value)} />
						</div>
						<button className="subsc-btn">Submit</button>
					  </div>
					</form>
				</div>
			</div>
		  </div>
		</div>
	  </div>
	</div>
	
	</>
	);
};

export default Login;
